//  vue状态管理
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js'
import actions from './actions.js';
//全局注册
Vue.use(Vuex);

//创建store对象
const store = new Vuex.Store({
    state:{
        topics:[
            {name: 'all',desc:'全部'},
            {name: 'good',desc:'精华'},
            {name: 'share',desc:'分享'},
            {name: 'ask',desc:'问答'},
            {name: 'job',desc:'招聘'},
            {name: 'dev',desc:'测试'}
          ],
          //未登录时的更多菜单
          unLoginedMoreMenus:{
            login:'登录'
          },
          //登录时的更多菜单
          loginedMoreMenus:{
            create:'发表新话题',
            center:'个人中心',
            msg:'消息通知',
            logout:'登出',
          },
          isLogined: false,//判断用户是否登录
          loginname:'',//登录用户名
          accesstoken:'',//登录用户的accesstoken
          author_id:'',//登录用户id
          isShowMoreMenus: false//判断菜单栏是否显示
    },
    getters:{
      moreMenus:function(state){
        if(state.isLogined){
            //用户处于登录状态,返回登录后等更多菜单
            return state.loginedMoreMenus;
        }
        return state.unLoginedMoreMenus;
      },
      getTopicDescByTab:function(state){
        //需要返回一个函数结构
        return function(tab){
          for (var index=0;index<state.topics.length;index++){
            if(state.topics[index].name == tab){
              return state.topics[index].desc;
            }
          }
        }
      }

    },
    mutations,
    actions
})

//对外输出
export default store;


