
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name:'user',
props:['loginname'],
components:{
Card,Group,Cell
},
data:function(){
return{
userinfo:null,
collect_topic:''
}
},
beforeRouteEnter:function (to, from, next) {
next(function(vm){

var app=this;
vm.$http.get('/user/'+to.params.loginname).then(function(response){

vm.userinfo = response.data.data;
});

vm.$http.get('/topic_collect/'+to.params.loginname).then(function(response){
vm.collect_topic=response.data.data;
})
});
},
beforeRouteUpdata: function(to, from, next) {
var app = this;
app.$http.get("/user/" + to.parmas.loginname).then(function(response) {

app.userinfo = response.data.data;
app.$http.get('/topic_collect/'+to.params.loginname).then(function(response){
app.collect_topic=response.data.data;
})

});

next();
}
}



